header .header__container {
    height: 100vh; 

    background-color: #0e82f9;
}

header .hamburger {
    display: inline-block;
    overflow: visible; 

    margin: 0;
    padding: 15px 15px;

    font: inherit;

    color: inherit;
    border: 0;
    background-color: transparent;

    cursor: pointer;
    transition-timing-function: linear;
    transition-duration: .15s;
    transition-property: opacity, -webkit-filter;
    transition-property: opacity, filter;
    transition-property: opacity, filter, -webkit-filter;
    text-transform: none;
}

header .hamburger:hover {
    opacity: .7;
}

header .hamburger-box {
    position: relative; 

    display: inline-block;

    width: 40px;
    height: 24px;
}

header .hamburger-inner {
    top: 50%;

    display: block;

    margin-top: -2px;
}

header .hamburger-inner,
header .hamburger-inner::before,
header .hamburger-inner::after {
    position: absolute;

    width: 40px;
    height: 4px;

    border-radius: 4px;
    background-color: #fff;

    transition-timing-function: ease; 
    transition-duration: .15s;
    transition-property: -webkit-transform;
    transition-property:         transform;
    transition-property:         transform, -webkit-transform;
}

header .hamburger-inner::before,
header .hamburger-inner::after {
    display: block; 

    content: '';
}

header .hamburger-inner::before {
    top: -10px;
}

header .hamburger-inner::after {
    bottom: -10px;
}

header .hamburger--slider .hamburger-inner {
    top: 2px;
}

header .hamburger--slider .hamburger-inner::before {
    top: 10px;

    transition-timing-function: ease;
    transition-duration: .15s; 
    transition-property: opacity, -webkit-transform;
    transition-property: transform, opacity;
    transition-property: transform, opacity, -webkit-transform;
}

header .hamburger--slider .hamburger-inner::after {
    top: 20px;
}

header .hamburger--slider.is-active .hamburger-inner {
    -webkit-transform: translate3d(0, 10px, 0) rotate(45deg);
            transform: translate3d(0, 10px, 0) rotate(45deg);
}

header .hamburger--slider.is-active .hamburger-inner::before {
    opacity: 0; 

    -webkit-transform: rotate(-45deg) translate3d(-5.71429px, -6px, 0);
            transform: rotate(-45deg) translate3d(-5.71429px, -6px, 0);
}

header .hamburger--slider.is-active .hamburger-inner::after {
    -webkit-transform: translate3d(0, -20px, 0) rotate(-90deg);
            transform: translate3d(0, -20px, 0) rotate(-90deg);
}

header .list {
