// font-family: "RobotoRegular";
@font-face {
    font-family: "RobotoRegular";
    src: url("../fonts/Roboto-Regular.eot");
    src: url("../fonts/Roboto-Regular.ttf") format("truetype"), url("../fonts/Roboto-Regular.woff") format("woff"), url("../fonts/Roboto-Regular.woff2") format("woff2");
    font-style: normal;
    font-weight: normal;
}

